.hero__overlay{
    margin-left: auto;
        margin-right: auto;
        background-size: cover;
        background-repeat: no-repeat;
        max-width: 1440px;
        // background-position: center;
        // background-image: url(../images/hero/hero_lg.png);
        padding-top: 800px;

        @include mq(mobile) {
                max-width: 768px;
                margin-left: auto;
                margin-right: auto;
                background-position: center;
                background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                    url(../images/hero/hero_sm.png);
                background-image:
                    linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                    -webkit-image-set(url(../images/hero/hero_sm.webp) 1x);
        
        
                @include mq(retina) {
                    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                        url(../images/hero/hero_sm@2x.png);
                    background-image:
                        linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                        -webkit-image-set(url(../images/hero/hero_sm.png) 2x);
                }
            }
        

        @include mq(desktop) {
                max-width: 1440px;
                margin-left: auto;
                margin-right: auto;
                background-position: center;
                background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                    url(../images/hero/hero_lg.png);
                background-image:
                    linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                    -webkit-image-set(url(../images/hero/hero_lg.webp) 1x);
        
        
                @include mq(retina) {
                    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                        url(../images/hero/hero_lg@2x.png);
                    background-image:
                    linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
                        -webkit-image-set(url(../images/hero/hero_lg.png) 2x);
                }
            }
}
