.hero__wrapper-txt{
    width: 100%;
    position: absolute;
    top: 190px;
    left: 0;
    display: flex;
    flex-direction: column;

    @include mq(desktop){
        top: 310px;
    }
}

.hero__title{
    font-weight: 400;
        font-size: 32px;
        line-height: 1.5;
                        color: $secondary-txt;
                        margin-bottom: 20px;
        @include mq(desktop){
            font-size: 48px;
                line-height: 1.04;
            
                margin-bottom: 30px;
        }
}

.hero__link{

    font-weight: 400;
        font-size: 20px;
    line-height: 1.45;
    color: $secondary-txt;
    width: 100%;
    padding: 12px 0;
        text-align: center;
        border: 1px solid $secondary-txt;

        @include mq(desktop){
    font-size: 22px;
        width: 194px;
        }

}