

.backdrop {
    // position: fixed;
    // // top: 0;
    // // left: 50%;
    // top: 0px;
    //     left: 0px;
    // // transform: translateX(-50%);
    // z-index: 100;
    // width: 100%;
    //     height: 100%;
    //     display: flex;
    //     overflow: hidden scroll;
    // background: rgba(0, 0, 0, 0.2);
    // opacity: 1;
    // visibility: visible;
    // pointer-events: initial;
    // transition: opacity 250ms linear, visibility 250ms linear;

    position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        overflow: hidden;
        overflow-y: auto;
        -webkit-overflow-scrolling: touch;
        display: flex;
        flex-flow: column nowrap;
justify-content: flex-start;        /* см. ниже */
        align-items: center;
        z-index: 99;
        /* Чтобы окно не прилипало к границе
        браузера установим отступы */
        padding: 30px 0;
}

.backdrop.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

.modal__wrap{
    flex-shrink: 0;
        flex-grow: 0;
        width: 100%;
        min-height: 100%;
        margin: auto;
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        justify-content: center;
}

.modal{
    width: 960px;
    padding: 50px;
    background-color: $background-main;
position: absolute;
    // top: 50%;
    left: 50%;
    margin-top: 20px;
    margin-bottom: 100px;

    transform: translateX(-50%);

    flex-shrink: 0;
        flex-grow: 0;
    margin: auto;
    overflow: visible;
    max-width: 100%;
    transition: transform 0.15s ease 0s, opacity 0.15s ease 0s;
        transform: scale(1);
        // opacity: 0;
}

.modal__btn{
    width: 20px;
    height: 20px;
    position: absolute;
    top: 20px;
    right: 25px;
}
.modal__icon{
    max-width: 20px;
}
.modal__wrapper{
    display: flex;
    
}
.modals__img{
    width: 370px;
}
.modal__wrapper-info{
    margin-left: 40px;
    width: 450px;
    margin-bottom: 20px;
}

.modal__subtitle{
    font-weight: 400;
        font-size: 30px;
        line-height: 1.46;

        margin-bottom: 10px;
}

.modal__price{
    font-weight: 400;
        font-size: 30px;
        line-height: 1.46;
        color:$accent-txt;
                margin-bottom: 10px;
}

.modal__txt-span{
    font-weight: 700;
        font-size: 20px;
        line-height: 1.45;
        
}

.modal__txt{
    font-weight: 400;
        font-size: 20px;
        line-height:1.45;
        margin-bottom: 10px;

    &:last-child{
        margin-bottom: 0px;
    }
    &:nth-child(3){
        margin-bottom: 0;
    }
        &:nth-child(6) {
            margin-bottom: 0;
        }
}
.modal__list-numbers,
.modal__list-dots{
    padding-left: 20px;
    margin-top: 0;
    // margin-bottom: 20px;
}

.modal__item-numbers, 
.modal__item-dots{
        font-weight: 400;
            font-size: 20px;
            line-height: 1.45;
}
.modal__item-dots{
    list-style:disc;
}